body {
  margin: 0px;
  background-image: url(2img.jpg);
  background-size: cover;
}
.container {
  margin: 0px 0px;
  height: 100%;
  width: 100%;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
  display: flex;
  align-items: center;
  justify-content: center;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.outer {
  height: 482px;
  width: 700px;
  background-color: grey;
  position: relative;
  border: 2px solid white;
}
.calc-title {
  content: "CALCULATOR";
  color: white;
  height: 60px;
  width: 95%;
  align-items: center;
  background-color: black;
  margin: 10px auto;
  border: 1px solid transparent;
}
.calc-title p {
  line-height: 0px;
  text-align: center;
  font-weight: bolder;
  font-size: xx-large;
  letter-spacing: 2px;
}
.calc-screen {
  content: "CALCULATOR";
  color: black;
  height: 80px;
  width: 95%;
  align-items: center;
  background-color: rgb(252, 255, 252);
  margin: 10px auto;
  border: 1px solid transparent;
  overflow: hidden;
}
#calc-screen-text {
  float: right;
}
.calc-button {
  content: "CALCULATOR";
  color: grey;
  height: 300px;
  width: 95%;
  align-items: center;
  background-color: grey;
  margin: 10px auto;
}
.calc-table {
  text-align: center;
  border-collapse: collapse;
}
.calc-btn-row {
  background-color: red;
}
.calc-btn-text {
  height: 60px;
  width: 162px;
  cursor: pointer;
  font-weight: bolder;
  color: white;
  font-size: 20px;
  background-color: rgb(156, 20, 43);
  border: 1px solid grey;
}
.calc-btn-text:active {
  background-color: white;
  color: black;
  transition: all ease-in 0.1s;
}
